@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Ubuntu+Mono:wght@700&family=Ubuntu:wght@400;700&display=swap");

:root {
  --color-body-bg: #03002c;
  --color-primary: #3431b6;
  --color-primary-dark: #28268d;
  --color-primary-light: #4745b8;
  --color-secondary: #dfcf3b;
  --color-white: white;
  --color-black: black;
  --color-grey: #a1a1a1;
  --color-line: #23226b;
  --color-input-bg: #100d35;
  --color-footer-bg: #161616;

  --font-primary: "Nunito", sans-serif;
  --font-secondary: "Ubuntu", sans-serif;
  --font-mono: "Ubuntu Mono", "Courier New", Courier, monospace;
}

html {
  font-family: var(--font-primary);
  font-size: 16px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--color-body-bg);
  color: var(--color-white);
}

::-webkit-scrollbar {
  width: 0.6rem;
  height: 0.6rem;
  &-track {
    background-color: #0f0e3a;
    border-radius: 100vw;
  }
  &-thumb {
    background-color: #a2ad0d;
    border-radius: 100vw;
    &:hover {
      background-color: #96a00c;
    }
    &:active {
      background-color: #cfdd11;
    }
  }
}

button {
  padding: 0.9em 1em;
  background-color: var(--color-primary);
  color: var(--color-white);
  border: none;
  outline: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  &:hover {
    background-color: var(--color-primary-light);
  }
  &:active {
    background-color: var(--color-primary-dark);
  }
  &:disabled {
    background-color: #333 !important;
  }
}

ul {
  list-style: none;
  li::before {
    content: "\2022"; /* Add content: \2022 is the CSS Code/unicode for a bullet */
    color: var(--color-secondary); /* Change the color */
    font-weight: bold; /* If you want it to be bold */
    display: none; /* Needed to add space between the bullet and the text */
    width: 1em; /* Also needed for space (tweak if needed) */
    //margin-left: -1em; /* Also needed for space (tweak if needed) */
  }
}

span.spinner {
  border: 0.2em solid #fff2;
  border-top: 0.2em solid var(--color-secondary);

  border-radius: 50%;
  width: 1em;
  height: 1em;
  animation: spin 1.5s ease infinite;
  display: inline-block;
  margin: 0 0.5em;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(360deg);
    border-right: 0.2em solid var(--color-secondary);
  }
  100% {
    transform: rotate(0deg);
  }
}
